
body{
    background-color: darkmagenta;
}
.App{
    max-width: 860px;
    margin: 40px auto;
}
.card-grid{
    margin-top: 10px;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 10px;
}
.card{
    position: relative;
}
.card img{
    display: block;
}
.card .front {
    transform: rotateY(90deg);
    position: absolute;
    transition-delay: 0.2s;
    transition: all ease-in 0.2s;
}
.flipped .front {
    transform: rotateY(0deg);
    transition-delay: 0.2s;

}
.flipped .back{
    transform: rotateY(90deg);
    transition-delay: 0.2s;
}
.btn
{
    width: 150px;
    height: 40px;
    background-color: darkmagenta;
    color: white;
    font-size: 20px;
    font-weight: 200px;
    box-shadow: 0 3px 10px #000,0 3px 7px #000, 0 3px 10px #000;
    position: absolute;
    left: 46%;
    top: 20;
}

 